<template>
  <div class="edit-news-item">
    <Tabs
      v-model="activeTab"
      :data="tabs"
      @update:model-value="onChangeTab"
    >
      <template #default="{ item }">
        {{ item.title }}
      </template>
    </Tabs>
    <div class="edit-news-item__body">
      <Text variant="p" mb="4px" mt="20px">
        {{ t('labels.title') }}
      </Text>
      <Input
        v-model="form.title"
        hide-shadow
        :placeholder="t('labels.title')"
      />
      <Text variant="p" mb="4px" mt="20px">
        {{ t('labels.description') }}
      </Text>
      <QuillEditor
        v-model="form.body"
        min-height="193px"
      />
      <Button
        type="primary"
        class="edit-news-item__actions"
        @click="onSave"
      >
        {{ t('common.saveChanges') }}
      </Button>
    </div>
  </div>
</template>

<script>
import { computed, reactive, ref } from 'vue'
import { useI18n } from 'vue-i18n'
import Tabs from '@/components/UIKit/Tabs'
import Text from '@/components/Text/Text'
import Input from '@/components/UIKit/Input'
import Button from '@/components/UIKit/Button'
import QuillEditor from '@/components/Editor/QuillEditor'
import { useActions } from '@/plugins/vuex'
import { MODULES_NAMES } from '@/common/modules'

export default {
  name: 'EditNewsItem',
  components: {
    Tabs,
    Text,
    Input,
    Button,
    QuillEditor
  },
  emits: ['edit'],
  props: {
    item: {
      type: Object,
      default: () => ({})
    }
  },
  setup (props, { emit }) {
    const { t } = useI18n()
    const {
      editHistoryRow
    } = useActions(MODULES_NAMES.NEWS)

    const form = reactive({
      ...props.item
    })

    const tabs = computed(() => [
      { title: t('languages.en'), key: 'en', icon: 'uk' },
      { title: t('languages.ru'), key: 'ru', icon: 'ru' }
    ])
    const activeTab = ref(tabs.value[0])

    const onChangeTab = (tab) => {
      activeTab.value = tab
    }

    const onSave = () => {
      editHistoryRow(form)
      emit('edit', form)
    }

    return {
      t,
      tabs,
      form,
      activeTab,
      onSave,
      onChangeTab
    }
  }
}
</script>

<style lang="scss">
.edit-news-item {
  padding: 10px 20px 20px 20px;

  &__actions {
    margin-top: 20px;
    padding: 13px 16px;
    font-size: 14px;
  }
  &__body {
    .input {
      max-width: 500px;
    }
  }
}
</style>
