<template>
  <div
    ref="el"
    class="quill-editor"
    :style="{
      '--quill-min-height':  minHeight
    }"
  >
    <slot name="toolbar"></slot>
    <div ref="editor"></div>
  </div>
</template>

<script>
import {
  ref,
  watch,
  reactive,
  onMounted,
  onBeforeUnmount
} from 'vue'
import { quillDefaultOptions, Quill } from '@/plugins/quill'

import 'quill/dist/quill.core.css' // import styles
import 'quill/dist/quill.snow.css' // for snow theme
import 'quill/dist/quill.bubble.css' // for bubble theme

export default {
  name: 'quill-editor',
  emits: ['update:modelValue', 'change', 'focus', 'blur', 'ready'],
  props: {
    content: {
      type: String,
      default: ''
    },
    modelValue: {
      type: String,
      default: ''
    },
    disabled: {
      type: Boolean,
      default: false
    },
    options: {
      type: Object,
      required: false,
      default: () => ({})
    },
    globalOptions: {
      type: Object,
      required: false,
      default: () => ({})
    },
    minHeight: {
      type: String,
      default: 'unset'
    }
  },
  setup (props, { emit }) {
    const _options = ref({})
    const _content = ref('')
    const $el = ref()
    const editor = ref()
    const quill = ref()
    const defaultOptions = reactive(quillDefaultOptions)

    const initialize = () => {
      if ($el.value) {
        // Options
        _options.value = {
          ...defaultOptions,
          ...props.globalOptions,
          ...props.options
        }
        // Instance
        quill.value = new Quill(editor.value, _options.value)

        quill.value.enable(false)
        // Set editor content
        if (props.modelValue || props.content) {
          quill.value.pasteHTML(props.modelValue || props.content)
        }
        // Disabled editor
        if (!props.disabled) {
          quill.value.enable(true)
        }
        // Mark model as touched if editor lost focus
        quill.value.on('selection-change', range => {
          if (!range) {
            emit('blur', quill.value)
          } else {
            emit('focus', quill.value)
          }
        })
        // Update model if text changes
        quill.value.on('text-change', () => {
          let html = editor.value.children[0].innerHTML
          const text = quill.value.getText()
          if (html === '<p><br></p>') html = ''
          _content.value = html
          emit('update:modelValue', _content)
          emit('change', { html, text, quill: quill.value })
        })
        // Emit ready event
        emit('ready', quill.value)
      }
    }
    watch(() => props.content, newVal => {
      if (quill.value) {
        if (newVal && newVal !== _content.value) {
          _content.value = newVal
          quill.value.pasteHTML(newVal)
        } else if (!newVal) {
          quill.value.setText('')
        }
      }
    })

    // Watch content change
    watch(() => props.modelValue, newVal => {
      if (quill.value) {
        if (newVal && newVal !== _content.value) {
          _content.value = newVal
          quill.value.pasteHTML(newVal)
        } else if (!newVal) {
          quill.value.setText('')
        }
      }
    })
    // Watch disabled change
    watch(() => props.disabled, newVal => {
      if (quill.value) {
        quill.value.enable(!newVal)
      }
    })

    onMounted(() => {
      initialize()
    })
    onBeforeUnmount(() => {
      quill.value = null
    })

    return {
      el: $el,
      editor
    }
  }
}
</script>

<style lang="scss">
.quill-editor {
  display: flex;
  flex-direction: column;

  .ql-editor {
    min-height: var(--quill-min-height);
  }
  .ql-toolbar {
    border-top-left-radius: 4px;
    border-top-right-radius: 4px;
    border: 1px solid #E6E6E6;

    &.ql-snow .ql-formats {
      margin-right: 5px;
    }
  }
  .ql-container {
    border-bottom-left-radius: 4px;
    border-bottom-right-radius: 4px;
    border: 1px solid #E6E6E6;
  }
}
</style>
